<template>
  <Teleport to="body">
    <Transition name="fade">
      <div v-if="show" class="create-modal__overlay" @click.self="close">
        <Transition name="slide-up">
          <div v-if="show" class="create-modal">
            <h3 class="create-modal__title">Новая страница</h3>
            <input
              ref="input"
              v-model="title"
              type="text"
              class="create-modal__input"
              placeholder="Название страницы"
              maxlength="200"
              @keyup.enter="create"
            />
            <div class="create-modal__actions">
              <button
                class="create-modal__btn create-modal__btn--cancel"
                @click="close"
              >
                Отмена
              </button>
              <button
                class="create-modal__btn create-modal__btn--create"
                :disabled="!title.trim() || loading"
                @click="create"
              >
                <span v-if="loading">Создание...</span>
                <span v-else>Создать</span>
              </button>
            </div>
          </div>
        </Transition>
      </div>
    </Transition>
  </Teleport>
</template>

<script>
export default {
  name: 'CreatePageModal',
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['close', 'create'],
  data() {
    return {
      title: '',
      loading: false,
    }
  },
  watch: {
    show(val) {
      if (val) {
        this.title = ''
        this.$nextTick(() => {
          this.$refs.input?.focus()
        })
      }
    },
  },
  methods: {
    close() {
      if (!this.loading) {
        this.$emit('close')
      }
    },
    async create() {
      if (!this.title.trim() || this.loading) return
      this.loading = true
      this.$emit('create', this.title.trim())
    },
    resetLoading() {
      this.loading = false
    },
  },
}
</script>

