<template>
  <div class="pages-view">
    <!-- Header -->
    <div class="pages-header">
      <div class="pages-header__icon">
        <v-icon icon="mdi-note-multiple-outline" size="28" />
      </div>
      <div class="pages-header__content">
        <h1 class="pages-header__title">Mini Notes</h1>
        <p class="pages-header__subtitle">Ваши заметки</p>
      </div>
      <button class="pages-header__settings" @click="goToSubscription">
        <v-icon icon="mdi-cog-outline" size="22" />
      </button>
    </div>

    <!-- Search -->
    <div class="pages-search">
      <div class="pages-search__wrapper">
        <v-icon icon="mdi-magnify" size="20" class="pages-search__icon" />
        <input
          v-model="searchQuery"
          type="text"
          class="pages-search__input"
          placeholder="Поиск страниц..."
          @input="onSearchInput"
        />
        <button
          v-if="searchQuery"
          class="pages-search__clear"
          @click="clearSearch"
        >
          <v-icon icon="mdi-close" size="18" />
        </button>
      </div>
    </div>

    <!-- Content -->
    <div class="pages-content">
      <!-- Loading -->
      <div v-if="loading" class="pages-loading">
        <div class="pages-loading__card" v-for="i in 3" :key="i">
          <div class="pages-loading__icon"></div>
          <div class="pages-loading__text">
            <div class="pages-loading__title"></div>
            <div class="pages-loading__meta"></div>
          </div>
        </div>
      </div>

      <!-- Empty state -->
      <EmptyState
        v-else-if="pages.length === 0 && !searchQuery"
        icon="mdi-note-plus-outline"
        title="Нет страниц"
        subtitle="Создайте первую страницу для ваших заметок"
        action-text="Создать страницу"
        @action="showCreateModal = true"
      />

      <!-- Empty search -->
      <EmptyState
        v-else-if="pages.length === 0 && searchQuery"
        icon="mdi-magnify"
        title="Ничего не найдено"
        subtitle="Попробуйте изменить запрос"
      />

      <!-- Pages list -->
      <transition-group v-else name="fade-up" tag="div" class="pages-list">
        <PageCard
          v-for="(page, index) in pages"
          :key="page.id"
          :page="page"
          :style="{ transitionDelay: index * 0.05 + 's' }"
        />
      </transition-group>
    </div>

    <!-- FAB -->
    <button class="pages-fab" @click="showCreateModal = true">
      <v-icon icon="mdi-plus" size="28" />
    </button>

    <!-- Create Modal -->
    <CreatePageModal
      ref="createModal"
      :show="showCreateModal"
      @close="showCreateModal = false"
      @create="createPage"
    />
  </div>
</template>

<script>
import PageCard from '@/components/pages/PageCard.vue'
import CreatePageModal from '@/components/pages/CreatePageModal.vue'
import EmptyState from '@/components/common/EmptyState.vue'
import { PagesCall } from '@/api/call'

export default {
  name: 'PagesListView',
  components: {
    PageCard,
    CreatePageModal,
    EmptyState,
  },
  data() {
    return {
      loading: true,
      pages: [],
      searchQuery: '',
      searchTimeout: null,
      showCreateModal: false,
    }
  },
  async mounted() {
    await this.loadPages()
  },
  methods: {
    async loadPages() {
      try {
        this.loading = true
        const response = await PagesCall.getPages({ page: 0, size: 50 })
        this.pages = response.data.content
      } catch (error) {
        console.error('Failed to load pages:', error)
      } finally {
        this.loading = false
      }
    },
    onSearchInput() {
      clearTimeout(this.searchTimeout)
      this.searchTimeout = setTimeout(() => {
        this.search()
      }, 300)
    },
    async search() {
      if (!this.searchQuery.trim()) {
        await this.loadPages()
        return
      }

      try {
        this.loading = true
        const response = await PagesCall.searchPages(this.searchQuery.trim(), {
          page: 0,
          size: 50,
        })
        this.pages = response.data.content
      } catch (error) {
        console.error('Failed to search pages:', error)
      } finally {
        this.loading = false
      }
    },
    clearSearch() {
      this.searchQuery = ''
      this.loadPages()
    },
    async createPage(title) {
      try {
        const response = await PagesCall.createPage({ title })
        this.$refs.createModal?.resetLoading()
        this.showCreateModal = false
        this.$router.push({ name: 'page-detail', params: { id: response.data.id } })
      } catch (error) {
        console.error('Failed to create page:', error)
        this.$refs.createModal?.resetLoading()
      }
    },
    goToSubscription() {
      this.$router.push({ name: 'subscription' })
    },
  },
}
</script>

