<template>
  <div class="page-card" @click="goToPage">
    <div class="page-card__icon-wrapper">
      <v-icon icon="mdi-note-text-outline" size="24" />
    </div>
    <div class="page-card__content">
      <h3 class="page-card__title">{{ page.title }}</h3>
      <div class="page-card__meta">
        <span class="page-card__blocks">
          <v-icon icon="mdi-view-grid-outline" size="14" />
          {{ page.blockCount }} {{ blocksLabel }}
        </span>
        <span class="page-card__date">{{ formattedDate }}</span>
      </div>
    </div>
    <v-icon icon="mdi-chevron-right" size="20" class="page-card__arrow" />
  </div>
</template>

<script>
export default {
  name: 'PageCard',
  props: {
    page: {
      type: Object,
      required: true,
    },
  },
  computed: {
    blocksLabel() {
      const count = this.page.blockCount
      if (count === 1) return 'блок'
      if (count >= 2 && count <= 4) return 'блока'
      return 'блоков'
    },
    formattedDate() {
      const date = new Date(this.page.updatedAt)
      const now = new Date()
      const diff = now - date
      const days = Math.floor(diff / (1000 * 60 * 60 * 24))

      if (days === 0) return 'Сегодня'
      if (days === 1) return 'Вчера'
      if (days < 7) return `${days} дн. назад`

      return date.toLocaleDateString('ru-RU', {
        day: 'numeric',
        month: 'short',
      })
    },
  },
  methods: {
    goToPage() {
      this.$router.push({ name: 'page-detail', params: { id: this.page.id } })
    },
  },
}
</script>

